<template>
    <div class="xscan-code">
        <qrcode-stream ref="qrs" :camera="camera" :torch="torch" :track="track" @decode="onDecode" @init="onInit">
            <i class="xicon icon-arrow-left" @click="todoBack()"></i>
            <p class="xscan-code-line"></p>
            <p class="xscan-code-tip" :class="{ 'error': error }">{{ error || tipMessage }}</p>
        </qrcode-stream>
    </div>
</template>

<script>
import { QrcodeStream } from 'vue-qrcode-reader'

export default {
    name: 'scanCode',
    components: { QrcodeStream },
    data () {
        return {
            error: ''
        }
    },
    props: {
        camera: {
            type: String,
            default: 'rear'
        },
        torch: {
            type: Boolean,
            default: false
        },
        track: {
            type: Function
        },
        tipMessage: {
            type: String,
            default: '请扫描二维码'
        }
    },
    // mounted () {
    //     this.$refs.qrs.onLocate = null
    // },
    methods: {
        /**
         * 监听扫码成功
         */
        onDecode (result) {
            console.log('扫码结果', result)
            this.$emit('decodeEvent', result)
        },
        /**
         * 监听初始化结果
         */
        async onInit (promise) {
            try {
                await promise
            } catch (error) {
                if (error.name === 'NotAllowedError') {
                    this.error = 'ERROR: 您需要授予相机访问权限'
                } else if (error.name === 'NotFoundError') {
                    this.error = 'ERROR: 这个设备上没有摄像头'
                } else if (error.name === 'NotSupportedError') {
                    this.error = 'ERROR: 所需的安全上下文(HTTPS、本地主机)'
                } else if (error.name === 'NotReadableError') {
                    this.error = 'ERROR: 相机被占用'
                } else if (error.name === 'OverconstrainedError') {
                    this.error = 'ERROR: 安装摄像头不合适'
                } else if (error.name === 'StreamApiNotSupportedError') {
                    this.error = 'ERROR: 此浏览器不支持流API'
                } else if (error.name === 'InsecureContextError') {
                    this.error = 'ERROR: 不支持HTTP协议访问'
                } else {
                    this.error = error.name
                }
                this.$emit('errorEvent', this.error, error)
            }
        },
        /**
         * 监听点击返回按钮
         */
        todoBack () {
            this.$emit('backEvent')
        }
    }
}
</script>

<style>
.xicon{display:inline-block;background-size:100% 100%;}
.xicon.icon-arrow-left{width:.2rem;height:.32rem;background-image:url(./images/arrow_left_white.png);}
.xscan-code{position:fixed;top:0;left:0;right:0;bottom:0;width:100%;height:100%;}
.xscan-code .xicon{position:absolute;top:.28rem;left:.3rem;}
.xscan-code-line{margin-left:5%;width:90%;height:.06rem;box-shadow:0 0 2rem #02e0fe;background:url(./images/scan-line.png) no-repeat center/ 100% 100%;animation:scan-move 4s linear infinite;}
.xscan-code-tip{position:absolute;top:85vh;width:100%;box-sizing:border-box;text-align:center;color:#FFF;font-size:.28rem;line-height:.39rem;}
.xscan-code-tip.error{color:red;}
@keyframes scan-move{0%{transform:translateY(20vh);opacity:1;}
50%{transform:translateY(50vh);opacity:1;}
100%{transform:translateY(80vh);opacity:0;}}
</style>
