<template>
  <div class="xdining-scan">
      <scan-code v-show="isScan" :camera="isScan ? 'rear' : 'off'" @backEvent="isScan = false" @decodeEvent="onDecode" @errorEvent="onError"></scan-code>
      <div v-show="!isScan" class="xscan-status">
          <div class="xscan-status-top" :class="{ 'error': !scanStatus }">
              <i class="xsst-icon"></i>
              <p class="xsst-title">{{ scanStatus ? '核销成功' : '核销失败' }}</p>
              <p class="xsst-desc" v-show="!scanStatus">{{ scanMessage }}</p>
          </div>
          <div class="xbutton-box">
              <a class="xbutton fill continue" @click="startScan()">{{ '继续扫码' }}</a>
              <a class="xbutton back" @click="backInfo()">{{ '返回' }}</a>
          </div>
      </div>
  </div>
</template>

<script>
import scanCode from '@/components/scanCode/scanCode'

export default {
  name: 'diningScan',
  components: { scanCode },
  data () {
      return {
          fid: sessionStorage.getItem('fid'),
          initError: false, // 初始化错误
          isScan: true, // 是否正在扫码
          scanStatus: false, // 扫码状态
          scanMessage: '请重新扫码', // 扫码状态提示语
          scanInfo: {}, // 扫码成功后的对象
          queryInfo: {} // 请求参数
      }
  },
  created () {
      this.queryInfo = this.$route.query
  },
  methods: {
      /**
       * 扫码初始化错误
       */
      onError (message) {
          this.initError = true
          this.isScan = false
          this.scanStatus = false
          this.scanMessage = message
      },
      /**
       * 后勤-餐饮
       * 用餐关系的核销接口 author：刘思远
       */
      onDecode (url) {
          this.isScan = false

          console.log(url);
      },
      /**
       * 跳转回信息页
       */
      backInfo () {
        console.log('back')
          // this.$router.replace({ path: '/dining/info', query: this.$route.query })
      },
      /**
       * 开始扫码
       */
      startScan () {
          this.isScan = true
      }
  }
}
</script>

